.background-services {
    background-image: url(../img/services-bcgr.png), linear-gradient(228.57deg, #323264 57.16%, #323264 57.17%, #643E72 94.46%);
    border-radius: 0px 0px 12px 12px;
    background-repeat: no-repeat;
    background-position: top;
    position: relative;
    margin-bottom: 40px;
    background-size: 100%;
        &::after {
            position: absolute;
            content: '';
            height: 10px;
            background-color: #CACADB;
            border-radius: 0px 0px 12px 12px;
            left: 20px;
            right: 20px;
            bottom: -10px;
            width: calc(100% - 40px);
        }
        &::before {
            position: absolute;
            content: '';
            height: 10px;
            background-color: #E7E7F2;
            border-radius: 0px 0px 12px 12px;
            bottom: -20px;
            left: 40px;
            width: calc(100% - 80px);
        }
}
